.card {
    background: #fff;
    border-radius: 50%;
    height: 255px;
    margin: 1rem;
    position: relative;
    width: 325px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: center;
    font-family: 'Proza Libre', sans-serif;

  }
  
  .card > .img-container {
    height: 60%;
    overflow: hidden;
    position: relative;
    top: 15%;
  }
  .card > .img-container > img {
    width: 60%;
    border-radius: 50%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 12px;
  }
  
  .card > .content > ul {
    list-style-type: none;
    /* padding: 0; */
  }
  
  .card > .content > ul > li {
    margin-top: 54px;
  }

  
  .card:hover {
	cursor: pointer;
}


  


